/**
 * Featured events
 *
 * @since 1.0.0
 */
.fse-featured-events {
  background: var(--fse-featured-background); }
  .fse-featured-events,
  .fse-featured-events a {
    color: var(--fse-featured-color); }
  .fse-featured-events.fse-has-background {
    padding-top: 2rem;
    padding-bottom: 2rem; }
    .fse-featured-events.fse-has-background .fse-event-link-overlay:hover,
    .fse-featured-events.fse-has-background .fse-event-link-overlay:focus,
    .fse-featured-events.fse-has-background .fse-event-link-overlay:not(.wp-block-button__link):focus {
      outline-color: var(--fse-featured-color); }
  .fse-featured-events.fse-has-background-image::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: var(--fse-featured-background-overlay);
    opacity: 0.8; }
  .fse-featured-events .fse-date {
    border-color: var(--fse-featured-color); }
  .fse-featured-events .fse-date-month {
    background-color: var(--fse-featured-color);
    color: var(--fse-featured-background); }

.fse-featured-events,
.fse-featured-events-wrap {
  position: relative; }

.fse-featured-events-wrap {
  z-index: 1; }
  .fse-featured-events-wrap .fse-featured-events-header {
    grid-column: var(--fse-column-cards-column-column); }

.fse-featured-body p {
  margin-top: 1rem; }

@media (min-width: 64rem) {
  .fse-featured-events.fse-has-background {
    padding-top: 4rem;
    padding-bottom: 4rem; }
  .fse-featured-events-header {
    text-align: right; } }

@media (min-width: 105rem) {
  .fse-featured-events.fse-has-background {
    padding-top: 6rem;
    padding-bottom: 6rem; } }
