/**
 * Event single
 *
 * @since 1.0.0
 */
.fse-event-single-header-wrap {
  display: grid;
  align-items: start;
  justify-items: start;
  gap: 1rem; }

.fse-event-single-thumbnail {
  width: 100%; }

.fse-event-content-wrap {
  display: grid;
  align-items: start;
  gap: 2rem; }
  @media (min-width: 80rem) {
    .fse-event-content-wrap {
      grid-template-columns: 1fr auto; } }

.fse-event-entry-content.entry-content {
  margin-top: 0; }

.fse-event-aside {
  display: grid;
  gap: 2rem; }
  @media (min-width: 64rem) and (max-width: 79.9375rem) {
    .fse-event-aside {
      grid-template-columns: repeat(2, 1fr); } }

.fse-event-aside-heading {
  margin-bottom: var(--fse-aside-heading-spacing); }

@media (min-width: 31.25rem) {
  .fse-event-single-header-wrap {
    grid-template-columns: auto 1fr;
    gap: 1.5rem; }
  .fse-event-registration-label {
    grid-column: span 2; } }

@media (min-width: 50rem) {
  .fse-event-single-header-wrap {
    grid-template-columns: auto 1fr auto; }
  .fse-event-registration-label {
    grid-column: span 1; } }

@media (min-width: 64rem) {
  .fse-event-single-header-wrap {
    gap: 2rem; } }

@media (min-width: 94rem) {
  .fse-event-content-wrap,
  .fse-event-aside {
    gap: 4rem; } }
